import React, { useEffect } from 'react';
import { Link } from "react-router-dom";
import { Header } from './components/Header';
import { Catalogue } from './components/Catalogue';
import { SeoMailing } from './components/SeoMailing';
import { Footer } from './components/Footer';
import { ModalsPro } from "./components/ModalsPro";
import photo_001 from './img/certificate.png';

export const Sertificate = (props) => {
    useEffect(() => {
        window.scrollTo(0, 0)
    })
    return (
        <>
            <Header pathname={props.history.location.pathname}>
                <Catalogue />
                <section className="header__navmob">
                    <h3 className="navmob__catalogue"> </h3>
                </section>
            </Header>
            <main>
                <section>
                    <ul className="flexstart breadcrumbs flexwrap">
                        <li><Link to="/"> &gt;</Link></li>
                        <li><Link to="About">  &gt;</Link></li>
                        <li></li>
                    </ul>
                    <article className="textleft certificate__section marginbot4020">
                        <h1 className="marginbot4020"></h1>
                        <p className="bold marginbot2010"> !</p>
                        <p className="marginbot2010">       10%</p>
                        <p className="marginbot4020">     ,    10%
                          .</p>
                        <p className="marginbot4020"> 10%    ,   -
                    audiovideomir.com.ua,     -.</p>
                        <p className="marginbot4020"> 10%       .</p>
                        <p className="marginbot4020">        .</p>
                        <p className="marginbot4020">      .</p>
                        <p className="marginbot4020">   ,     .</p>
                        <p className="marginbot4020">         
                             .</p>
                        <div className="certificate__img marginbot2010 textcenter">
                            <img src={photo_001} alt="" />
                        </div>
                    </article>
                </section>
                <SeoMailing />
            </main>
            <Footer />
            <ModalsPro pathname={props.history.location.pathname} />
        </>
    )
}