import React, { useEffect } from 'react';
import { Link } from "react-router-dom";
import icons from '../icons.svg';

export const Footer = () => {
    useEffect(() => {
        document.getElementById('arrowUp').onclick = () => {
            window.scrollBy({
                top: -window.scrollY,
                behavior: 'smooth'
            })
        }
        window.addEventListener('scroll', () => {
            document.getElementById('arrowUp').hidden = document.documentElement.clientHeight / 2 > document.documentElement.scrollTop
        })
    }, [])
    return (
        <footer>
            <section className="footer__section">
                <div className="footer__desk flexcol">
                    <div className="flexbet">
                        <div>
                            <Link to="/" replace>
                                <svg>
                                    <use xlinkHref={`${icons}#logo`}></use>
                                </svg>
                            </Link>
                        </div>
                        <ul className="flexcenter">
                            <li><Link to="Category" replace> </Link></li>
                            <li><a href="_glossary.html"></a></li>
                            <li><Link to="Services" replace></Link></li>
                            <li><Link to="Hall" replace> </Link></li>
                            <li><Link to="Delivery" replace>  </Link></li>
                            <li><Link to="Credit" replace></Link></li>
                            <li><Link to="Actions" replace></Link></li>
                            <li><Link to="Warranty" replace>  </Link></li>
                            <li><Link to="About" replace> </Link></li>
                        </ul>
                    </div>
                    <p>    ,   , ,  : , 
                    , , , , , , , ,
                    -, , , -, , , , ,
                    ,  , , , , , , , , ,
                    , , , -, , , , , ,
                    , , , , , , , , , ,
                    , , , , , , , , , ,
             , ,   .</p>
                </div>
                <div className="footer__mob">
                    <div className="horizont"></div>
                    <ul>
                        <li className="global__list"><a href=" "
                            className="global__href footer__href flexcenter"><i></i><i></i></a>
                            <ul>
                                <li><Link to="Category" replace> </Link></li>
                                <li><a href="_glossary.html"></a></li>
                                <li><Link to="Services" replace></Link></li>
                                <li><Link to="Hall" replace> </Link></li>
                            </ul>
                        </li>
                    </ul>
                    <div className="horizont"></div>
                    <ul>
                        <li className="global__list"><a href=" "
                            className="global__href footer__href flexcenter"><i></i><i></i></a>
                            <ul>
                                <li><Link to="Delivery" replace>  </Link></li>
                                <li><Link to="Credit" replace></Link></li>
                                <li><Link to="Actions" replace></Link></li>
                                <li><Link to="Warranty" replace>  </Link></li>
                                <li><Link to="About" replace> </Link></li>
                            </ul>
                        </li>
                    </ul>
                    <div className="horizont"></div>
                </div>
                <div className="footer__social flexcenter justcenter">
                    <a href=" ">
                        <svg>
                            <use xlinkHref={`${icons}#insta`}></use>
                        </svg>
                    </a>
                    <a href=" ">
                        <svg>
                            <use xlinkHref={`${icons}#facebook`}></use>
                        </svg>
                    </a>
                    <a href=" ">
                        <svg>
                            <use xlinkHref={`${icons}#youtube`}></use>
                        </svg>
                    </a>
                    <a href=" ">
                        <svg>
                            <use xlinkHref={`${icons}#telegram`}></use>
                        </svg>
                    </a>
                </div>
                <p>Copyright  1999-2020 Pear Solution</p>
            </section>
            <i id="arrowUp" hidden=""></i>
        </footer>
    )
}
